import { PrjInfoContainer } from './ProjectInfoStyled';
function ProjectInfo({ challenge }) {
	switch (
		challenge.challengeTargetCode //1: , 2:   , 3:  /, 4: 
	) {
		case 1: {
			//
			return (
				<PrjInfoContainer>
					<table>
						<tbody>
							<tr>
								<th className="prjinfo_header"></th>
								<td>{challenge.ownerName ? challenge.ownerName : 'no data'}</td>
								<th></th>
								<td>{challenge.companyA ? challenge.companyA : 'no data'}</td>
							</tr>
							<tr>
								{' '}
								<th> / </th>
								<td>{challenge.companyB ? challenge.companyB : 'no data'}</td>
								<th></th>
								<td>{challenge.url ? challenge.url : 'no data'}</td>
							</tr>
						</tbody>
					</table>
				</PrjInfoContainer>
			);
		}
		case 2: {
			// 
			return (
				<PrjInfoContainer>
					<table>
						<thead>
							<th></th>
							<th> </th>
							<th></th>
							<th></th>
						</thead>
						<tbody>
							<tr>xxxx</tr>
							<tr>xxxx</tr>
							<tr>Xxxxx</tr>
							<tr>xxxxx</tr>
						</tbody>
					</table>
				</PrjInfoContainer>
			);
		}
		case 3: {
			//  / 
			return (
				<PrjInfoContainer>
					<table>
						<thead>
							<th></th>
							<th> </th>
							<th></th>
							<th></th>
						</thead>
						<tbody>
							<tr>xxxx</tr>
							<tr>xxxx</tr>
							<tr>Xxxxx</tr>
							<tr>xxxxx</tr>
						</tbody>
					</table>
				</PrjInfoContainer>
			);
		}
		case 4: {
			//
			return (
				<PrjInfoContainer>
					<table>
						<thead>
							<th></th>
							<th> </th>
							<th> </th>
							<th></th>
							<th></th>
							<th> </th>
						</thead>
						<tbody>
							<tr> </tr>
							<tr></tr>
							<tr></tr>
							<tr>200000 KRW</tr>
							<tr> </tr>
							<tr>, </tr>
						</tbody>
					</table>
				</PrjInfoContainer>
			);
		}
		default: {
			return <div>no data</div>;
		}
	}
}
export default ProjectInfo;
