import React, { useState } from 'react';
import ReactQuill from 'react-quill';
// import Quill from 'quill';
// import ImageResize from '@looop/quill-image-resize-module-react';
// Quill.register('modules/ImageResize', ImageResize);

const QuillTextEditor = ({ handleText }) => {
	const [editorText, setEditorText] = useState(
		'<ul><li> :</li></ul><p><br></p><ul><li>  :</li></ul><p><br></p><ul><li>  :</li></ul><p><br></p><ul><li>  :</li></ul><p><br></p><ul><li>  : </li></ul><p><br></p><ul><li>  :</li></ul><p><br></p><ul><li>  :</li></ul><p><br></p><ul><li>  :</li></ul><p><br></p><ul><li>  :</li></ul><p><br></p><ul><li> :</li></ul>',
	);

	const editorModule = {
		toolbar: [
			[{ header: [1, 2, false] }],
			['bold', 'italic', 'underline'],
			[
				{ list: 'ordered' },
				{ list: 'bullet' },
				{ indent: '-1' },
				{ indent: '+1' },
			],
			['image'],
			[{ align: [] }, { color: [] }],
			['clean'],
		],
		// ImageResize: {
		//   modules: ['Resize'],
		// },
	};
	const editorFormats = [
		'header',
		'bold',
		'italic',
		'underline',
		'list',
		'bullet',
		'indent',
		'image',
		'align',
		'color',
	];

	//   html    , formdata    ; (DB )

	// const editorModule = {
	//   toolbar:{
	//     container: "#toolbar",
	//     handlers: {
	//       image: handleImageData,
	//     }
	//   }
	// }

	// const handleImageData = () =>{
	//   const input =document.createElemnet("input")

	//   input.setAttribute("type", "file")
	//   input.setAttribute("aceept", "image/*")
	//   input.click();

	//   input.onchange = async () => {
	//     if(input.files){
	//       let file: any = input.files[0]
	//       let formData = new FormData();

	//       fomrData.append("image", file)

	//       let fileName = file.name;

	//       console.log(fromData)
	//     }
	//   }
	// }

	return (
		<ReactQuill
			className="notice_editor"
			modules={editorModule}
			formats={editorFormats}
			value={editorText}
			onChange={(value) => {
				setEditorText(value);
				handleText(value);
			}}
		/>
	);
};

export default QuillTextEditor;
